#toggle {
	display: none;
}

.hamburger {
	cursor: pointer;
	right: 2.4rem;
	padding: 1rem 0;
	align-self: center;
	z-index: 30;
	right: 5vw;
	display: block;
	padding: 1.2rem 0;
}
@media screen and (min-width: 992px) {
	.hamburger {
		display: none;
	}
}
.hamburger__icon,
.hamburger__icon::before,
.hamburger__icon::after {
	height: 0.4rem;
	width: 2.5rem;
	display: block;
	background: #242d52;
	transition: all 0.3s ease-in-out;
}
.hamburger__icon {
	position: relative;
	transition: all 0.3s ease-in-out;
}
.hamburger__icon::before,
.hamburger__icon::after {
	position: absolute;
	content: "";
	top: -0.8rem;
}
.hamburger__icon::after {
	top: 0.8rem;
}

.overlay {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	transition: opacity 400ms ease;
	height: 100vh;
	background: linear-gradient(
		27deg,
		rgba(0, 0, 0, 0.7) 0%,
		rgba(0, 0, 0, 0) 100%
	);
}

#toggle:checked ~ .hamburger .hamburger__icon {
	transform: rotate(45deg);
}

#toggle:checked ~ .hamburger .hamburger__icon::before {
	transform: rotate(-90deg);
	top: -0.1rem;
}

#toggle:checked ~ .hamburger .hamburger__icon::after {
	opacity: 0;
	transform: rotate(45deg);
}

#toggle:checked ~ .overlay {
	opacity: 1;
	pointer-events: all;
	width: 100%;
	position: fixed;
	z-index: 3;
}

#toggle:checked ~ ul {
	transform: scale(1);
}

